@mixin plain() {
  &,
  &:link,
  &:visited {
    @content;
  }
}

@mixin hover() {
  &:hover {
    @content;
  }
}

@mixin active() {
  &:active,
  &.active {
    @content;
  }
}

@mixin focus() {
  &:focus {
    @content;
  }
}

@mixin disabled() {
  &:disabled,
  &.disabled {
    @content;
  }
}
