/* stylelint-disable scss/no-global-function-names */
$base: (
  font-family:            (-apple-system, blinkmacsystemfont, "Segoe UI", helvetica, arial, sans-serif),
  font-family-code:       (menlo, monaco, consolas, andale mono, lucida console, courier new, monospace),

  font-size-root:         16px,
  font-size-root-sm:      14px,

  font-size:              1rem,
  font-size-xl:           1.5rem,
  font-size-lg:           1.25rem,
  font-size-md:           1rem,
  font-size-sm:           .85rem,
  font-size-xs:           .7rem,

  font-size-h1-xl:        3rem,
  font-size-h2-xl:        2.25rem,
  font-size-h3-xl:        1.75rem,
  font-size-h4-xl:        1.75rem,
  font-size-h5-xl:        1.5rem,
  font-size-h6-xl:        1.5rem,

  font-size-h1-lg:        2.5rem,
  font-size-h2-lg:        2rem,
  font-size-h3-lg:        1.5rem,
  font-size-h4-lg:        1.5rem,
  font-size-h5-lg:        1.25rem,
  font-size-h6-lg:        1.25rem,

  font-size-h1:           2rem,
  font-size-h2:           1.5rem,
  font-size-h3:           1.25rem,
  font-size-h4:           1.2rem,
  font-size-h5:           1rem,
  font-size-h6:           1rem,

  font-size-h1-sm:        1.5rem,
  font-size-h2-sm:        1.25rem,
  font-size-h3-sm:        1.15rem,
  font-size-h4-sm:        1.15rem,
  font-size-h5-sm:        1rem,
  font-size-h6-sm:        1rem,

  font-size-h1-xs:        1.05rem,
  font-size-h2-xs:        1rem,
  font-size-h3-xs:        .95rem,
  font-size-h4-xs:        .9rem,
  font-size-h5-xs:        .85rem,
  font-size-h6-xs:        .85rem,

  font-weight:            400,
  font-weight-bold:       700,

  line-height-xl:         2,
  line-height-lg:         1.8,
  line-height:            1.6,
  line-height-sm:         1.4,
  line-height-xs:         1.2,

  spacer:                 1rem,

  border-radius-lg:       .8rem,
  border-radius:          .4rem,
  border-radius-sm:       .2rem
);

$spacers: (
  0:                      0,
  1:                      map-get($base, spacer) * .25,
  2:                      map-get($base, spacer) * .5,
  3:                      map-get($base, spacer),
  4:                      map-get($base, spacer) * 1.5,
  5:                      map-get($base, spacer) * 3,
  6:                      map-get($base, spacer) * 4.5
);

//     unset       middle       top
// | ---------- | --------- | --------- |
// 0           400         999          -
$z-indexes: (
  modal: 997,
  sidebar-active: 998,
  popover: 600,
  sidebar: 400,
  header: 400,
  actions: 399,
);

$layout: (
  scrollbar-width:        16px,
  header-height:          3.4rem,
  header-height-sm:       3rem,
  header-nav-left-mobile: 150px,
  content-max-width:      950px,
  sidebar-width:          250px,
  sidebar-width-sm:       48px,
  sidebar-header-height:  3rem,
  aside-width-sm:         300px,
  aside-width:            350px,
  aside-width-lg:         411px
);

//     sm       md       lg
// | ------ | ------ | ------ |
// 0       500      1024      -
// see https://v5.bootcss.com/docs/layout/breakpoints/

$breakpoint-default: "md";
$responsive: (
  sm:                      0,
  md:                      500px,
  lg:                      1024px
);

$animation: (
  duration:               .4s,
  duration-sm:            .2s,
);

$animation-timing-function: (
  1:                      ease-in-out,
  2:                      cubic-bezier(.68, -.55, .27, 1.55),
  3:                     cubic-bezier(.215, .61, .355, 1)
);

$clickable: (
  transition:             all .15s ease-in-out
);

$button-height-xl:        2.8rem;
$button-height-lg:        2.3rem;
$button-height:           1.9rem;
$button-height-sm:        1.5rem;
$button-height-xs:        1.2rem;

$button: (
  padding-y-xl:           ($button-height-xl - map-get($base, font-size-xl)) / 2,
  padding-x-xl:           $button-height-xl / 3,
  padding-y-lg:           ($button-height-lg - map-get($base, font-size-lg)) / 2,
  padding-x-lg:           $button-height-lg / 3,
  padding-y-md:           ($button-height - map-get($base, font-size)) / 2,
  padding-x-md:           $button-height / 3,
  padding-y-sm:           ($button-height-sm - map-get($base, font-size-sm)) / 2,
  padding-x-sm:           $button-height-sm / 3,
  padding-y-xs:           ($button-height-xs - map-get($base, font-size-xs)) / 2,
  padding-x-xs:           $button-height-xs / 3,

  pill-radius:            6rem,

  circle-diameter-xl:     $button-height-xl,
  circle-diameter-lg:     $button-height-lg,
  circle-diameter-md:     $button-height,
  circle-diameter-sm:     $button-height-sm,
  circle-diameter-xs:     $button-height-xs,

  font-weight:            map-get($base, font-weight-bold)
);

$image: (
  width-xl:  20em,
  width-lg:  16rem,
  width:     12rem,
  width-sm:  8rem,
  width-xs:  4rem,
  width-xxs: 3.5rem
);

$menu: (
  horizontal-spacer: 1,
  horizontal-item-vertical-spacer: 1
);

$dark: (
  l-1: 4%,
  1: 14%,
  2: 15%,
  3: 18%,
  4: 30%
);

$light: (
  l-1: 6%,
  1: 16%,
  2: 18%,
  3: 21%,
  4: 30%
);

