/* stylelint-disable scss/no-global-function-names */
$color-one: #ffbe0b;
$color-two: #fb5607;
$color-three: #ff006e;
$color-four: #8338ec;
$color-five: #3a86ff;
$color-brilliant: linear-gradient(90deg, $color-one, $color-two);
$color-rainbow: linear-gradient(90deg, $color-one, $color-two, $color-three, $color-four, $color-five);
$color-rainbow-y: linear-gradient(180deg, $color-one, $color-two, $color-three, $color-four, $color-five);

$main-color-1: #fb7299;

$text-color-3: #505050;
$main-color-3: #fff;

$background-color: #fff;

$text-color-theme-light-d: #000;
$text-color-theme-light: #222;
$text-color-theme-light-l: #888;

$theme-light: (
  // main colors
  main-color-1: $main-color-1,
  text-color-1: #fff,

  main-color-2: #fca24d,
  text-color-2: #fff,

  main-color-3: $main-color-3,
  text-color-3: $text-color-3,

  main-color-theme-light: rgba(#000, .9),
  main-color-theme-dark:  rgba(#fff, .9),

  // page background
  background-color: $background-color,

  // text colors
  text-color-theme-light-d: $text-color-theme-light-d,
  text-color-theme-light:   $text-color-theme-light,
  text-color-theme-light-l: $text-color-theme-light-l,

  text-color-theme-dark-d:  #fff,
  text-color-theme-dark:    rgba(#fff, .95),
  text-color-theme-dark-l:  rgba(#fff, .85),

  text-color-d: $text-color-theme-light-d,
  text-color: $text-color-theme-light,
  text-color-l: $text-color-theme-light-l,

  text-background-color: rgba(#000, .05),

  // header and footer colors
  header-text-color: $text-color-3,
  header-background: $main-color-3,
  // header-background: linear-gradient(60deg,#f6f9fa 0%, #e6f9ea 100%),

  footer-text-color: $text-color-3,
  footer-background: linear-gradient(to bottom, $background-color, #ebf2ff 100%),
  footer-background-top: $color-rainbow,
  // footer-background: linear-gradient(12deg,#f2f2f6 0%, $main-color-3 100%),

  // border and shadow colors
  border-color-d:   mix(#000, $background-color, 40%),
  border-color:     mix(#000, $background-color, 20%),
  border-color-l:   mix(#000, $background-color, 10%),
  border-color-light: $color-brilliant,
  decorate-color:   rgba(#000, .1),
  modal-color:       rgba(#000, .9),
  select-color: rgba($main-color-1, .4),

  // function colors
  green:           #5baa34,
  blue:            #1c7cd4,
  yellow:          #c9771f,
  red:             #da3d45,
  text-color-function: rgba(#fff, .8),

);

// logo colors
@import "./logo-colors";
$theme-light: map-merge($theme-light, $logo-colors);
